import React from 'react';
import styled, { injectGlobal, css, keyframes } from 'styled-components';
import PropTypes from 'prop-types';

const textFadeIn = keyframes`
  from {
    color: rgba(0,0,0,0);
  }
  to{
    color: #281c1c;
  }
`;

// eslint-disable-next-line no-unused-expressions
injectGlobal`
  .${textFadeIn}{
    animation: ${textFadeIn} 1.2s;
  }
`;

const Dialog = styled.div`
  position: absolute;
  left: 50%;
  bottom: 30px;
  width: 850px;
  transform: translateX(-50%);
  padding: 15px 50px 30px;
  font-size: 30px;
  font-family: PingFangTC;
  line-height: normal;
  text-align: justify;
  color: #281c1c;
  background-color: rgba(255, 255, 255, 0.8);

  transition: bottom 1s, transform 1s;
  ${({ only }) => only && Dialog.only};
`;

Dialog.only = css`
  bottom: 50%;
  transform: translate(-50%, 50%);
`;

const Body = styled.div`
  letter-spacing: normal;
`;

const Author = styled.div`
  letter-spacing: normal;
  text-align: right;
`;

export default class Message extends React.Component {
  static propTypes = {
    message: PropTypes.string,
    author: PropTypes.string,
    only: PropTypes.bool,
  };

  static defaultProps = {
    message: '',
    author: '',
    only: false,
  };

  componentDidUpdate() {
    this.dom.classList.add(textFadeIn);
  }

  onAnimationEnd = () => {
    this.dom.classList.remove(textFadeIn);
  };

  setRef = (dom) => { this.dom = dom; };

  render() {
    const { setRef, onAnimationEnd } = this;
    const { message, author, only } = this.props;

    return (
      <Dialog only={only} innerRef={setRef} onAnimationEnd={onAnimationEnd}>
        <Body>{message}</Body>
        <Author>{author}</Author>
      </Dialog>
    );
  }
}



